@import url(https://fonts.googleapis.com/css2?family=Metamorphous&family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */


body {
  font-family: 'Metamorphous', cursive;
  margin: 0;
  font-size: 1.1em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.App {
  max-width: 50vw;
  margin: 40px auto;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background: #c23866;
  color: #fff;
}

.card-grid {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
}
.card {
    height: 90%;
    width: 90%;
    position: relative;
    margin: 0 auto;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* flip card using transform - front styling */
.card .front {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in 0.25s;
    position: absolute;
}

.flipped .front {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    transition-delay: 0.25s;
}

/* flip card using transform - back styling */
.card .back {
    transition: all ease-in 0.25s;
    transition-delay: 0.25s;
}

.flipped .back {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition-delay: 0s;
}
